$--font-dm-sans: "DM Sans";
$--font-sf-pro: "Outfit";

/* font sizes */
$--font-size-base: 16px;
$--font-size-5xl: 24px;
$--font-size-13xl: 32px;
$--font-size-mini: 15px;
$--font-size-29xl: 48px;

/* Colors */
$--color-white: #fff;
$--color-black: #000;
$--color-gray-100: rgba(255, 255, 255, 0.5);
$--color-darkslategray-100: #404040;
$--color-darkslategray-200: rgba(64, 64, 64, 0.5);
$--color-cornflowerblue: #3d92ed;

/* Border radiuses */
$--br-81xl: 100px;

.transform_career_paths {
  margin: 0;
  font-size: 50px;
  font-weight: 600;
}

.bespoke_solutions_for,
.transform_career_paths_container {
  position: relative;
  font-size: 55px;
  text-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
}

.bespoke_solutions_for {
  font-size: 18px;
  display: inline-block;
}

.transform_career_paths_with_ca_parent {
  position: relative;
  width: 100%;
  height: 800px;
  max-height: 90vh;
  overflow: hidden;
  background-image: url(../../assets/images/aboutUs/frame1.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  justify-content: center;
  display: flex;
  align-items: center;
  gap: 20px;
  flex-direction: column;
  font-size: $--font-size-29xl;
  color: $--color-white;
  font-family: $--font-sf-pro;
}