$--font-dm-sans: "DM Sans";
$--font-sf-pro: "Outfit";

/* font sizes */
$--font-size-base: 16px;
$--font-size-5xl: 24px;
$--font-size-13xl: 32px;
$--font-size-mini: 15px;
$--font-size-29xl: 48px;

/* Colors */
$--color-white: #fff;
$--color-black: #000;
$--color-gray-100: rgba(255, 255, 255, 0.5);
$--color-darkslategray-100: #404040;
$--color-darkslategray-200: rgba(64, 64, 64, 0.5);
$--color-cornflowerblue: #3d92ed;

/* Border radiuses */
$--br-81xl: 100px;

.transform_career_paths {
  color: #4b9bee;
  font-weight: 700;
  font-size: 38px;
  // font-family: SF $--font-sf-pro;
}

.bespoke_solutions_for {
  font-weight: 400;
  size: 16px;
  font-family: SF $--font-sf-pro;
  color: #848484;
  width: 100%;
}

.bespoke_solutions_for {
  font-size: 28px;
  font-weight: 400;
  display: inline-block;
}

.transform_career_paths_with_ca_parent {
  width: 100%;
  height: 600px;
  overflow: hidden;
  background-image: url(../../assets/images/aboutUs/frame4.png);
  background-size: cover;
  background-repeat: no-repeat;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  flex-direction: column;
  font-size: $--font-size-29xl;
  color: $--color-white;
  font-family: $--font-sf-pro;
}